            .right 收藏
        .item.form(@click="jumpToFrom")
            | 询最低价
</template>

<script>
export default {
    methods: {
        showMoreParams () {
            alert('more');
        },
        jumpToFrom () {
            this.$router.push({
                path: '/car/form'
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.page-car-detail {
    .base-info {
        padding: 1rem;
        .title {
            font-size: 1.2rem;
            line-height: 1.4rem;
        }
        .info {
            color: #999;
            padding-top: 0.5rem;
        }
        .cur-price {
            font-size: 2rem;
            color: #f04c03;
            line-height: 2rem;
            padding: 0.8rem 0;
        }
        .origin-price {
            color: #999;
        }
    }
    .dealer {
        padding: 1rem;
        .name {
            font-size: 1.2rem;
            line-height: 1.4rem;
        }
        .number {
            color: #999;
            font-size: 0.87rem;
            padding: 0.5rem 0;
            border-bottom: 1px solid #f5f5f5;
        }
        .phone,
        .location {
            padding: 1rem 0;
            border-bottom: 1px solid #f5f5f5;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .text {
                width: 20rem;
            }
            .icon {
                flex-shrink: 0;
                color: #f04c03;
            }
        }
        .audio {
            padding-top: 1rem;
            display: flex;
            align-items: center;
            .voice {
                width: 8rem;
                border: 1px solid #ccc;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0.4rem 0.4rem;
                border-radius: 0.2rem;
                margin-left: 1rem;
                i {
                    color: #f04c03;
                }
            }
        }
    }
    .tips {
        color: red;
        padding-left: 2rem;
        padding-bottom: 1.5rem;
    }
    .desc {
        padding: 0 1rem 1rem 2rem;
        .title {
            padding: 0.5rem 0;
            font-weight: bold;
        }
        .content {
            line-height: 1.4rem;
        }
    }
    .prize {
        padding: 0 1rem 1rem 2rem;
        .score {
            padding: 0 0 0.5rem;
            font-weight: bold;
            color: rgb(37, 155, 36);
        }
        .tags {
            display: flex;
            align-items: flex-start;
            flex-wrap: wrap;
            .item {
                font-size: 0.87rem;
                padding: 0.3rem 0.6rem;
                background-color: rgb(37, 155, 36);
                color: #fff;
                margin-right: 0.5rem;
                margin-bottom: 0.5rem;
            }
        }
    }
    .imgs {
        padding: 0 1rem 1rem 1rem;
        img {
            width: 100%;
            display: block;
            margin-bottom: 1rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .bottom {
        .item {
            width: 33.33%;
        }
        .phone,
        .collect {
            background: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            border-right: 1px solid #eee;
            .right {
                font-size: 0.87rem;
                margin-left: 0.5rem;
            }
        }
        .form {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
        }
    }
}
</style>



